.App {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  color: #fffdfa;
}

.contextMenu {
  position: absolute;
  width: 200px;
  background-color: rgba(30, 30, 30, 0.6);
  border-radius: 10px;
  padding: 10px;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  z-index: 1;
  color: #fffdfa;
}

.contextMenu .contextMenuItem {
  padding: 10px;
  cursor: pointer;
}

.contextMenu .contextMenuItem:hover {
  background-color: rgba(30, 30, 30, 0.6);
}
